.subtotal{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: max-content;
  padding: 20px;
  background-color: white;
  border: 1px solid white;
  border-radius: 3px;
}

.subtotal > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }